:root {
  --white: #ffffff;
  --gray: #cfcfcf;
  --blue: #00c4ff;
}
.formCon {
  margin-top: 20px;
  button {
    width: 200px;
    height: 50px;
    background: var(--blue);
    border-radius: 25px;
    font-size: 22px;
    color: white;
    margin-top: 35px;
    margin-bottom: 50px;
    box-shadow: 0px 0px 15px 2px var(--blue),
      0px 0px 8px 0px rgb(0 191 255 / 0%);
  }
}
.logoCon {
  margin-top: 45px;
  img {
    width: 55px;
    height: 55px;
    margin-right: 10px;
  }
  h1 {
    font-size: 30px;
    color: var(--blue);
    margin-left: 10px;
    text-shadow: 0 0 5px var(--blue), 2px 2px 2px rgb(206 89 55 / 0%);
  }
}

.inputCon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  label {
    color: var(--gray);
    font-size: 22px;
  }
  input {
    border: 2px solid var(--blue);
    background: none;
    border-radius: 25px;
    color: white;
    width: 280px;
    height: 45px;
    margin-top: 5px;
    padding: 0px 15px 0px 15px;
    font-size: 23px;
    text-align: center;
    outline: none;
    text-shadow: 0 0 5px var(--white), 2px 2px 2px rgb(206 89 55 / 0%);
    box-shadow: 0px 0px 5px 1px var(--blue), 0px 0px 8px 0px rgb(0 191 255 / 0%);
  }
}
.subTitle {
  h1 {
    margin-top: 10px;
    margin-bottom: 10px;
    color: var(--gray);
    font-size: 18px;
    text-align: center;
    text-shadow: 0 0 5px var(--white), 2px 2px 2px rgb(206 89 55 / 0%);
  }
}
.quizPrevCon {
  .quizCon {
    border: 3px solid #00b5ff;
    box-shadow: 0px 0px 4px 2px #00b5ff, 0px 0px 8px 0px rgb(0 191 255 / 0%),
      inset 0px 0px 4px 2px #00b5ff;
    width: 80%;
    padding: 10px;
    border-radius: 10px;
    color: var(--gray);
    text-shadow: 0 0 5px var(--white), 2px 2px 2px rgb(206 89 55 / 0%);
    font-size: 18px;
  }
}
.logOutCon {
  bottom: 0;
  position: absolute;
  button {
    width: 150px;
    height: 40px;
    background: var(--blue);
    border-radius: 25px;
    font-size: 18px;
    color: white;
    margin-top: 35px;
    margin-bottom: 50px;
    box-shadow: 0px 0px 7px 2px var(--blue), 0px 0px 8px 0px rgb(0 191 255 / 0%);
  }
}
